<template>
  <section class="wrapper-step-three">
    <form action="" @submit.stop.prevent="handleSubmit">
      <!-- part-one -->
      <div class="recipient-info part part-one">
        <div class="title"></div>
        <div class="form-row">
          <label for="username">&emsp;&emsp;</label>
          <input
            type="text"
            name="username"
            id="username"
            placeholder=""
            minlength="2"
            v-model="recipientInfo.username"
            required
          />
        </div>
        <div class="form-row">
          <label for="telephone"></label>
          <input
            type="text"
            name="telephone"
            id="telephone"
            placeholder=""
            minlength="10"
            maxlength="10"
            v-model="recipientInfo.telephone"
            required
          />
        </div>
        <div class="form-row" v-if="initialPaymentMethod.includes('')">
          <label for="store"></label>
          <select name="area" id="area" v-model="recipientInfo.pickUpStore">
            <option value="none" selected disabled></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>
        </div>
        <div class="form-row" v-else>
          <label for="address"></label>
          <input
            type="text"
            name="address"
            id="address"
            placeholder=""
            v-model="recipientInfo.address"
          />
        </div>
      </div>
      <!-- part-two -->
      <div
        class="credit-card part part-two"
        v-if="initialPaymentMethod === ''"
      >
        <div class="title"></div>
        <div class="form-row">
          <label for="cardnumber"></label>
          <input
            type="text"
            name="cardnumber"
            id="cardnumber"
            placeholder=""
            minlength="16"
            maxlength="16"
            v-model="creditCard.cardNumber"
          />
        </div>
        <div class="form-row">
          <label for="expdate">&emsp;</label>
          <input
            type="text"
            name="expdate"
            id="expdate"
            placeholder="MM/YY"
            v-model="creditCard.validDate"
          />
        </div>
        <div class="form-row">
          <label for="cvv"></label>
          <input
            type="text"
            name="cvv"
            id="cvv"
            placeholder=""
            minlength="3"
            maxlength="3"
            v-model="creditCard.ccv"
          />
        </div>
        <div class="form-row credit-notice">
          <p>
            
            VISAMasterCardJCB
          </p>
        </div>
      </div>
      <!-- part-three -->
      <div class="invoice-type part part-three">
        <div class="title"></div>
        <div class="form-row">
          <div :class="['form-line', { active: invoiceType === 'personal' }]">
            <input
              type="radio"
              name="type"
              id="personal"
              value="personal"
              v-model="invoiceType"
            />
            <label for="personal"> - </label>
          </div>
          <div :class="['form-line', { active: invoiceType === 'company' }]">
            <input
              type="radio"
              name="type"
              id="company"
              value="company"
              v-model="invoiceType"
            />
            <label for="company"> - </label>
          </div>
          <div :class="['form-line', { active: invoiceType === 'donate' }]">
            <input
              type="radio"
              name="type"
              id="donate"
              value="donate"
              minlength="8"
              maxlength="8"
              v-model="invoiceType"
            />
            <label for="donate"></label>
          </div>
          <div :class="['form-line', { active: invoiceType === 'barcode' }]">
            <input
              type="radio"
              name="type"
              id="barcode"
              value="barcode"
              v-model="invoiceType"
            />
            <label for="barcode">-</label>
          </div>
        </div>
        <div class="form-row">
          <div class="text-personal" v-show="invoiceType === 'personal'">
            
          </div>
          <div class="text-company" v-show="invoiceType === 'company'">
            <div>
              <label for="company-number">&emsp;</label>
              <input
                type="text"
                name="company-number"
                id="company-number"
                placeholder=""
                minlength="8"
                maxlength="8"
                v-model="invoice.company"
              />
            </div>
            <p>
              lativ
            </p>
          </div>
          <div class="text-donate" v-show="invoiceType === 'donate'">
            <label for="donation"></label>
            <select name="donation" id="donation" v-model="invoice.donate">
              <option value="none" selected disabled></option>
              <option value="">
                
              </option>
              <option value="">
                
              </option>
              <option value="">
                
              </option>
            </select>
          </div>
          <div class="text-barcode" v-show="invoiceType === 'barcode'">
            <div>
              <label for="barcode-number"></label>
              <input
                type="text"
                name="barcode-number"
                id="barcode-number"
                placeholder="()"
                v-model="invoice.barcode"
              />
            </div>
            <div>
              <label for="barcode-number-confirm"></label>
              <input
                type="text"
                name="barcode-number-confirm"
                id="barcode-number-confirm"
                placeholder="()"
                v-model="confirmBarcode"
              />
            </div>
          </div>
        </div>
      </div>
      <!-- part-four -->
      <div class="return-notice part part-four">
        <div class="form-row">
          <input
            type="checkbox"
            name="return-notice"
            id="return-notice"
            value="agreeFastRefund"
            v-model="recipientInfo.fastRefund"
          />
          <label for="return-notice"
            > lativ
            </label
          >
        </div>
      </div>
      <!-- submit-panel -->
      <div class="submit-panel">
        <label class="submit-cart-label">
          <input type="submit" class="submit-cart-input" value="" />
          </label
        >
      </div>
    </form>
    <div class="button-panel">
      <router-link :to="{ name: 'shopping', params: { step: 2 } }"
        ><button class="button button-primary">
          
        </button></router-link
      >
    </div>
  </section>
</template>

<script>
export default {
  props: {
    initialPaymentMethod: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      invoiceType: "",
      recipientInfo: {
        username: "",
        telephone: "",
        pickUpStore: "",
        address: "",
        fastRefund: false,
      },
      creditCard: {
        cardNumber: "",
        validDate: "",
        ccv: "",
      },
      invoice: {
        company: "", // 
        donate: "",
        barcode: "",
      },
      confirmBarcode: "",
    };
  },  
  methods: {
    handleSubmit(e) {
      // 
      const method = this.initialPaymentMethod;

      if (this.recipientInfo.username === "") {
        alert("");
        return;
      } else if (this.recipientInfo.telephone === "") {
        alert("");
        return;
      }

      // -- 
      if (method.includes("")) {
        if (this.recipientInfo.pickUpStore === "") {
          alert("");
          return;
        }
        // -- 
      } else {
        if (this.recipientInfo.address === "") {
          alert("");
          return;
        }

        if (method === "") {
          for (let key in this.creditCard) {
            if (this.creditCard[key] === "") {
              alert("");
              return;
            }
          }
        }
      }

      // -- 
      if (this.invoiceType === "") {
        alert("");
        return;
      } else {
        if (this.invoiceType === "company") {
          if (this.invoice.company === "") {
            alert("");
            return;
          }
        } else if (this.invoiceType === "donate") {
          if (this.invoice.donate === "") {
            alert("");
            return;
          }
        } else if (this.invoiceType === "barcode") {
          if (this.invoice.barcode === "") {
            alert("");
            return;
          }
          if (this.invoice.barcode !== this.confirmBarcode) {
            alert("");
            return;
          }
        }
      }
      
      const form = e.target;
      const formData = new FormData(form);
      this.$emit('after-save', formData)
    },
  },
};
</script>

<style lang="scss" scoped>
/* input */
input {
  &[type="text"] {
    border: none;
    margin-left: 20px;

    &::placeholder {
      color: #cccccc;
    }

    &:focus::placeholder {
      color: transparent;
    }
  }
}

label {
  margin-bottom: 0;
}

select {
  border-color: #cccccc;
  margin-left: 20px;

  &:invalid {
    color: #cccccc;
  }
}

.wrapper-step-three {
  font-size: 15px;
  background-color: #fbfbfb;
  position: relative;

  form {
    .part {
      &:not(:last-child) {
        border: 1px solid #ebebeb;
        margin: 20px 0;
        background-color: #ffffff;
      }

      .title,
      .form-row {
        height: 60px;
        display: flex;
        align-items: center;

        &.credit-notice {
          height: 80px;
        }
      }

      .title {
        font-size: 18px;
        border-bottom: 1px solid #ebebeb;
        margin: 0;
        padding: 0 30px;
      }
    }

    .part-one,
    .part-two {
      .form-row {
        margin: 0 30px;

        &:not(:last-child) {
          border-bottom: 1px solid #ebebeb;
        }
      }
    }

    .part-three {
      .form-row {
        margin: 0 30px;
        gap: 10px;

        &:nth-child(2) {
          height: 140px;
        }

        &:nth-child(3) {
          height: 100%;
          padding: 20px 0;
        }

        .text-personal {
          color: #cccccc;
        }

        .text-company,
        .text-barcode {
          width: 100%;
          height: 100%;
          display: flex;
          flex-flow: column wrap;
          justify-content: space-evenly;

          div {
            margin-bottom: 10px;
          }

          p {
            color: #c14948;
          }
        }

        .text-barcode {
          input {
            width: 100%;
            border-bottom: 1px solid #cccccc;
            margin: 0;
            line-height: 30px;
          }
        }
      }

      .form-line {
        border: 1px solid #cccccc;
        border-radius: 4px;
        color: #cccccc;
        width: 170px;
        height: 50px;
        text-align: center;
        line-height: calc(50 / 15);
        flex-grow: 1;

        &.active {
          border-color: #5e3b25;
          color: #5e3b25;
        }

        input[type="radio"] {
          -webkit-appearance: none;
        }

        label {
          width: 100%;
          height: 100%;
          cursor: pointer;
        }
      }
    }

    .part-four {
      .form-row {
        margin: 0 30px;
        height: 60px;
        font-size: 13px;
        color: #a8a8a8;
        flex-wrap: nowrap;

        input {
          -webkit-appearance: none;
          border: 1px solid #cccccc;
          width: 16px;
          height: 16px;
          margin-right: 10px;
          flex-shrink: 0;
          position: relative;

          &:checked::after {
            content: "";
            width: 6px;
            height: 8px;
            border: solid #676767;
            border-width: 0 2px 2px 0;
            transform: rotate(45deg);
            position: absolute;
            top: 2px;
            left: 4px;
          }
        }
      }
    }

    .submit-panel {
      order: 5;
      position: absolute;
      bottom: 0;
      right: 0;
      text-align: center;

      label {
        width: 180px;
        height: 40px;
        background-color: #5e3b25;
        border-radius: 4px;
        font-size: 15px;
        line-height: 22px;
        color: #ffffff;
        cursor: pointer;
      }

      input {
        visibility: hidden;
      }
    }
  }
}

/* button-panel */
.button-panel {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;

  .button-primary {
    width: 180px;
    height: 40px;
    border-radius: 4px;
    font-size: 15px;
    color: #ffffff;
    background-color: #c3a789;
  }
}

@media screen and (min-width: 768px) {
  .wrapper-step-three {
    form {
      .part-three {
        .form-row:nth-child(2) {
          height: 70px;
        }
      }
    }
  }
}
</style>